import { FC } from 'react';

import { Wrapper } from './RichText.styles';
import { RichTextProps } from './RichText.types';

/**
 * This component is used for styling and laying out long-form content such as
 * that found in the body of an article or blog post.
 *
 * It is exported along with an InlineContainer component which can be used to
 * wrap other components and ensure that they adhere to the flow of the document
 */
const RichText: FC<RichTextProps> = ({ children }) => (
  <Wrapper>{children}</Wrapper>
);

export default RichText;
